      isFixFloorParam: function (floorName) {
        // 利用正则实现模糊查询
        if(this.searchFloorParam===undefined || this.searchFloorParam===null){
          return true;
        }
        return (floorName!=null && floorName !==undefined && this.searchFloorParamReg.test(floorName));
      },
