      onTabsClicked: function (tabName) {
        // 处理滚动条
        let baseInfoNiceScroll = $(".building-base-info-border").getNiceScroll();
        let floorInfoNiceScroll = $("#floor_info_left_list").getNiceScroll();
        // 预留
//        let personInfoNiceScroll = $(".building-base-info-border").getNiceScroll();
        let deviceInfoNiceScroll = $(".auto-height-table .ivu-table-body").getNiceScroll();
        switch(tabName) {
          case 'baseInfo':
            if(floorInfoNiceScroll !== undefined && floorInfoNiceScroll !== null && floorInfoNiceScroll.length !== 0) {
              floorInfoNiceScroll.hide();
            }
            if(deviceInfoNiceScroll !== undefined && deviceInfoNiceScroll !== null && deviceInfoNiceScroll.length !== 0) {
              deviceInfoNiceScroll.hide();
            }
            baseInfoNiceScroll.show();
            setTimeout(() => {
              baseInfoNiceScroll.resize();
            });
            break;
          case 'floorInfo':
            if(baseInfoNiceScroll !== undefined && baseInfoNiceScroll !== null && baseInfoNiceScroll.length !== 0) {
              baseInfoNiceScroll.hide();
            }
            if(deviceInfoNiceScroll !== undefined && deviceInfoNiceScroll !== null && deviceInfoNiceScroll.length !== 0) {
              deviceInfoNiceScroll.hide();
            }
            if(floorInfoNiceScroll === undefined || floorInfoNiceScroll === null || floorInfoNiceScroll.length === 0) {
              $("#floor_info_left_list").niceScroll({
                cursorcolor: "rgb(99, 114, 131)",
                cursorwidth: "7px",
                cursoropacitymax: 0.4,
                cursorborder: "", // css definition for cursor border
                cursorborderradius: "",
                hidecursordelay: 1000,
              });
            } else {
              floorInfoNiceScroll.show();
              setTimeout(() => {
                floorInfoNiceScroll.resize();
              });
            }
            break;
          case 'personInfo':
            if(floorInfoNiceScroll !== undefined && floorInfoNiceScroll !== null && floorInfoNiceScroll.length !== 0) {
              floorInfoNiceScroll.hide();
            }
            if(deviceInfoNiceScroll !== undefined && deviceInfoNiceScroll !== null && deviceInfoNiceScroll.length !== 0) {
              deviceInfoNiceScroll.hide();
            }
            if(baseInfoNiceScroll !== undefined && baseInfoNiceScroll !== null && baseInfoNiceScroll.length !== 0) {
              baseInfoNiceScroll.hide();
            }
            break;
          case 'deviceInfo':
            if(floorInfoNiceScroll !== undefined && floorInfoNiceScroll !== null && floorInfoNiceScroll.length !== 0) {
              floorInfoNiceScroll.hide();
            }
            if(baseInfoNiceScroll !== undefined && baseInfoNiceScroll !== null && baseInfoNiceScroll.length !== 0) {
              baseInfoNiceScroll.hide();
            }
            if(deviceInfoNiceScroll === undefined || deviceInfoNiceScroll === null || deviceInfoNiceScroll.length === 0) {
              $(".auto-height-table .ivu-table-body").niceScroll({
                cursorcolor: "rgb(99, 114, 131)",
                cursorwidth: "7px",
                cursoropacitymax: 0.4,
                cursorborder: "", // css definition for cursor border
                cursorborderradius: "",
                hidecursordelay: 1000,
              });
            } else {
              deviceInfoNiceScroll.show();
              setTimeout(() => {
                deviceInfoNiceScroll.resize();
              });
            }
            break;
        }
      },
      changeFloor:function(floorId){
        if(floorId===''){
          this.device.posX=null;
          this.device.posY=null;
          this.defaultFloorId = undefined;
          $("#device-container").empty();
          return;
        }
//        if(this.updFloorIndex==0){
//          this.updFloorIndex++;
//          return;
//        }
        $("#device-container").removeClass('no-img');
        this.currentPlanformUrl = null;
        for(var i =0; i<this.buildFloors.length; i++){
          if(floorId == this.buildFloors[i].floorId){
            this.currentPlanformUrl = this.buildFloors[i].planformUrl;
            break;
          }
        }
        // 修改楼层，设置坐标点为空
        if(this.defaultFloorId !== floorId) {
          this.device.posX=null;
          this.device.posY=null;
          this.defaultFloorId = undefined;
        }
        if((this.device.posX===undefined || this.device.posX===null) && this.currentPlanformUrl !== null && this.currentPlanformUrl !== undefined) {
          // 设备未定位，初始化定位状态
          $("#device-container").addClass("cursor-device");
          this.selectedDevice = this.device;
        }
        if (this.currentPlanformUrl != undefined && this.currentPlanformUrl != null) {
          //新设备追加场合
//          if(this.device.deviceId===undefined || this.device.deviceId===null){
//            //坐标点重置成一个固定的坐标点，左上角
//            this.device.posX=null;
//            this.device.posY=null;
//            //url重置成该楼层的url，如果没有楼层平面图，那么就重置成固定的图片。
//            this.device.planformUrl=planformUrl;
//          }
          this.floorDeviceList=[];
          this.floorDeviceList.push(this.device);
          Vue.nextTick(() => {
            this.drawFloorMap('device-container', server.api.img.replace('{imageId}',this.currentPlanformUrl));
          });
        } else {
          $("#device-container").empty();
          $("#device-container").addClass("no-img");
        }
      },
