      /** Start 楼层点设备点位 **/
      // 画楼层定位的地图
      drawFloorMap: function (divId, imgUrl) {
        let image = new Image();
        image.src=imgUrl;
        let self = this;
        image.onload = function() {
          self.drawFloorMapWithRange(divId, imgUrl, image.width, image.height);
        }
      },
      drawFloorMapWithRange(divId, imgUrl, imgWidth, imgHeight) {
        let container = $("#" + divId);
        container.empty();
        let self = this;
        this.center = ol.proj.transform([121.609643, 38.913305], 'EPSG:4326', 'EPSG:3857');
        // 计算楼层平面图映射到地图上的范围(minX, minY, maxX, maxY)，图片像素为 1000 * 719，保持比例的情况下，把分辨率放大一些
        this.extent = [this.center[0] - imgWidth * 1000 / 2, this.center[1] - imgHeight * 1000 / 2, this.center[0] + imgWidth * 1000 / 2, this.center[1] + imgHeight * 1000 / 2];
        this.map = new ol.Map({
          view: new ol.View({
            center: this.center,
            zoom: 7
          }),
          target: divId,
          controls: []
        });
        this.map.addLayer(new ol.layer.Image({
          source: new ol.source.ImageStatic({
            url: imgUrl, // 楼层平面图
            imageExtent: this.extent     // 映射到地图的范围
          })
        }));
        Vue.nextTick(() => {
          // 画点
          this.drawFloorSensors(divId);
          // 注册点击事件
          this.map.on('singleclick', function(value){
            if(self.selectedDevice === undefined) {
              return;
            }
            // 不可编辑模式下不能修改设备位置
            if(!self.editable) {
              return;
            }
            let x = value.coordinate[0];
            let y = value.coordinate[1];
            // 创建一个设备图标需要的Feature，并设置位置(extentLeft + picX, extentTop + picY)
            let device = new ol.Feature({
              geometry: new ol.geom.Point([x, y]),
              device: self.selectedDevice,
            });
            // 为feature设置id值，方便查询
            device.setId(self.selectedDevice.deviceId);
            // 设置Feature的样式，使用火警图标
            device.setStyle(new ol.style.Style({
              image: new ol.style.Icon({
                src: self.device_shield
              }),
              text: new ol.style.Text({
                // font: '10px sans-serif' 默认这个字体，可以修改成其他的，格式和css的字体设置一样
                offsetY: -20,
                text: self.selectedDevice.deviceName,
                fill: new ol.style.Fill({
                  color: '#19be6b'
                }),
                stroke: new ol.style.Stroke({    //文字边界宽度与颜色
                  color: '#fff',
                  width: 3
                })

              })
            }));
            let index = self.floorDeviceList.indexOf(self.selectedDevice);
            self.floorDeviceList[index].posX = x;
            self.floorDeviceList[index].posY = y;

            // 将原来的删除
            if(self.selectedDevice.deviceId !== undefined) {
              let oldDeviceFeature = self.deviceLayer.getSource().getFeatureById(self.selectedDevice.deviceId);
              if(oldDeviceFeature !== undefined && oldDeviceFeature !== null) {
                self.deviceLayer.getSource().removeFeature(oldDeviceFeature);
              }
            } else {
              // 创建设备删除原来的点，清空图层
              self.deviceLayer.getSource().clear();
            }
            self.deviceLayer.getSource().addFeature(device);
            $(".dustbin-div").css("display", "none");
            self.selectedDevice = undefined;
            $("#" + divId).removeClass("cursor-device");
            $(".dustbin-div").removeClass("cursor-device");
          });
          // 图标点击事件
          let selectSingleClick = new ol.interaction.Select();
          this.map.addInteraction(selectSingleClick);
          let self = this;
          selectSingleClick.on('select', function(e) {
            selectSingleClick.getFeatures().clear();
            // 不可编辑模式下不能修改设备位置
            if(!self.editable) {
              return;
            }
            Vue.nextTick(() => {
              $(".dustbin-div").css("display", "block");
              self.deviceSelected(e.selected[0].get("device"), divId);
            });
          });
        });
      },
      // 画探头点 不将未定位的设备画上去
      drawFloorSensors: function (divId) {
        // 添加设备Feature到layer上，并把layer添加到地图中
        this.deviceLayer = new ol.layer.Vector({
          source: new ol.source.Vector(),
          extent: this.extent,
        });
        // 画点，单个设备或多个设备
        for (let index in this.floorDeviceList) {
          let device = this.floorDeviceList[index];
          if (device.posX === undefined || device.posX === null) {
            // 该设备不在平面图上
            continue;
          }
          // 创建一个设备图标需要的Feature，并设置位置(extentLeft + picX, extentTop + picY)
          let deviceFeature = new ol.Feature({
            geometry: new ol.geom.Point([device.posX, device.posY]),
            device: device
          });
          deviceFeature.setId(device.deviceId);
          // 设置Feature的样式，使用火警图标
          let color = '#4bbd4b';
          let deviceSrc = this.device_shield;
          deviceFeature.setStyle(new ol.style.Style({
            image: new ol.style.Icon({
              src: deviceSrc,
            }),
            text: new ol.style.Text({
              // font: '10px sans-serif' 默认这个字体，可以修改成其他的，格式和css的字体设置一样
              offsetY: -20,
              text: device.deviceName,
              fill: new ol.style.Fill({
                color: color
              }),
              stroke: new ol.style.Stroke({    //文字边界宽度与颜色
                color: '#fff',
                width: 3
              })
            })
          }));
          this.deviceLayer.getSource().addFeature(deviceFeature);
        }
        this.map.addLayer(this.deviceLayer);
      },
      initFloorModal: function(divId) {
        // 注册点击事件，除了地图位置的点击，将移除设备
        let self = this;
        $(".dustbin-div").click(function(e) {
          if (e.target.classList.contains("ol-unselectable") || self.selectedDevice === undefined) {
            return;
          } else {
            self.removeFromPlan(self.selectedDevice, divId);
          }
        });
      },
      removeFromPlan: function(device, divId) {
        // 删除地图上的设备
        let deviceFeature = this.deviceLayer.getSource().getFeatureById(device.deviceId);
        if(deviceFeature !== undefined && deviceFeature !== null) {
          this.deviceLayer.getSource().removeFeature(deviceFeature);
          $("#" + divId).removeClass("cursor-device");
          $(".dustbin-div").removeClass("cursor-device");
          // 处理 floorDeviceList
          let index = this.floorDeviceList.indexOf(this.selectedDevice);
          this.floorDeviceList[index].posX = null;
          this.floorDeviceList[index].posY = null;
          this.selectedDevice = undefined;
          $(".dustbin-div").css("display", "none");
          this.$Message.success("从平面图中移除设备成功！");
        }
      },
      deviceSelected: function(device, divId) {
        $("#" + divId).addClass("cursor-device");
        $(".dustbin-div").addClass("cursor-device");
        this.selectedDevice = device;
      },
      saveFloorDevice: function() {
        this.$http.put(server.api.organization.building.updDevicesPos, this.floorDeviceList).then((response)=>{
