        //设备类型加载
        this.getDeviceTypeList();
        //建筑加载
        this.getBuildList();
        //楼层加载
        this.getFloors();
      },
      loadSystemCode: function () {
        this.buildingId = this.$route.query.buildingId;
        let categories=[];
        categories.push('BUILDING_TYPE');
        categories.push('BUILDING_STRUCTURE_TYPE');
        categories.push('BUILDING_NATURE');
        categories.push('BUILDING_FIRE_DANGEROUSNESS');
        categories.push('BUILDING_FIRE_RESISTANCE_RATING');
        this.$http.get(server.api.organization.sysCode.getSysCode+'?categories[]='+categories).then((response)=>{
          this.systemCodeList=response.body.data;
          this.buildingCategoryList=_.filter(this.systemCodeList, {'codeCategory': 'BUILDING_TYPE'});
          this.structureTypeList=_.filter(this.systemCodeList, {'codeCategory': 'BUILDING_STRUCTURE_TYPE'});
          this.usingNatureList=_.filter(this.systemCodeList, {'codeCategory': 'BUILDING_NATURE'});
          this.fireDangerLevelList=_.filter(this.systemCodeList, {'codeCategory': 'BUILDING_FIRE_DANGEROUSNESS'});
          this.fireResistanceRatingList=_.filter(this.systemCodeList, {'codeCategory': 'BUILDING_FIRE_RESISTANCE_RATING'});
        },(error)=>{
          this.$Message.error("系统数据获取失败");
        })
      },
      getDeviceTypeList: function () {
        this.$http.get(server.api.organization.device.getDeviceTypeList).then((response) => {
          this.deviceTypeList = response.body.data;
        }, (error) => {
          this.$Message.error("设备类型加载失败");
        })
      },
      getBuildList: function () {
        this.$http.get(server.api.organization.buildingList.list.replace('{orgId}', this.organizationId)).then((response) => {
          this.buildingList = response.body.data;
        }, (error) => {
          this.$Message.error("建筑物列表加载失败");
        })
      },
      getIotGatewayIdList: function (buildingId) {
        //模拟量设备网关
        this.$http.get(server.api.organization.device.getIotGatewayIdList.replace('{buildingId}', buildingId) + "?type=" + "CCS-A16").then((response) => {
          this.iotGatewayList1 = response.body.data;
        }, (error) => {
          this.$Message.error("建筑物列表加载失败");
        });

        //普通消防设备网关
        this.$http.get(server.api.organization.device.getIotGatewayIdList.replace('{buildingId}', buildingId) + "?type=" + "CCS-D16").then((response) => {
          this.iotGatewayList2 = response.body.data;
        }, (error) => {
          this.$Message.error("建筑物列表加载失败");
        });

      },

      returnPage: function () {
        this.$router.push({
          path: '/company/building_list',
          query: {isInit: false}
        });
      },
      handleClickUpload() {
        // TODO 弹出层confirm不好用
//           this.$Modal.confirm({
//             title: '重新上传平面图',
//             content: '<p>重新上传平面图将删除该楼层/分区已添加的探头</p><p>是否继续？</p>',
//             onOk: () => {
//                 return true;
//             },
//             onCancel: () => {
//               return false;
//             }
//           });
      },

      render1(item) {
        return item.label;
      },
      handleChange1(newTargetKeys, direction, moveKeys) {
        console.log(newTargetKeys);
        console.log(direction);
        console.log(moveKeys);
        this.bldUsersKey = newTargetKeys;
        this.setBldUsers();
      },

      setBldUsers: function () {
        this.bldUsers = [];
        for (var i = 0; i < this.bldUsersKey.length; i++) {
          let key = this.bldUsersKey[i];
          for (var k = 0; k < this.orgUsers.length; k++) {
            let user = this.orgUsers[k];
            if (key == user.key) {
              user.buildingId = this.buildingId;
              this.bldUsers.push(user);
            }
          }
        }
      },
      handleUploadSuccess: function (response, file) {
        let imgUrl = response.data;
        for (let i = 0; i < this.floors.length; i++) {
          if (this.floors[i].floorId === this.selectedFloor.floorId) {
            this.selectedFloor.planformUrl=imgUrl;
            break;
          }
        }
        this.$Message.success("上传成功！");
        Vue.nextTick(() => {
          this.drawMap('floor_base_info_container', server.api.img.replace('{imageId}',imgUrl));
        });
      },
      handleUploadFormatError: function (file) {
        this.$Notice.warning({
          title: '文件格式不正确',
          desc: '文件 ' + file.name + ' 格式不正确，请上传 jpg 或 png 格式的图片。'
        });
      },
      handleUploadExceedMaxSize: function (file) {
        this.$Notice.warning({
          title: '超出文件大小限制',
          desc: '文件 ' + file.name + ' 太大，不能超过 2M。'
        });
      },
      changeModalBuild: function (buildingId) {
        if(buildingId===''){
          this.buildFloors =[];
          return;
        }
        this.getIotGatewayIdList(buildingId);
        this.$http.get(server.api.organization.building.getFloors.replace('{buildingId}',buildingId)).then((response) => {
          this.buildFloors = response.body.data;
        }, (error) => {
          this.$Message.error("建筑物楼层加载失败");
        })
      },
      cancel:function () {
        this.$refs['deviceFrom'].resetFields();
        this.typeCode = undefined;
        this.addDeviceModal=false;
      },
      addNewDevice: function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.device.deviceType === 'smart_smoke_detector' || this.device.deviceType === 'smart_gas_detector'){
              this.device.iotGatewayId='SYSTEM-SENSOR-SMART-SENSOR_GATEWAY-01';
            };
            this.device.pid=localStorage.default_pid;
            this.device.orgId=this.organizationId;
            this.iots();
            this.$http.post(server.api.organization.device.addDevice, this.device).then((response) => {
              if(!response.body.meta.success){
                this.$Message.error(response.body.meta.message);
                return;
              }
              this.getDevices(this.pageNum);
              this.addDeviceModal = false;
              this.$Message.success('添加成功！');
              this.$refs['deviceFrom'].resetFields();
            }, (error) => {
              this.$Message.error('修改失败！');
            })
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },

      iots:function () {
        if(this.typeCode===undefined){
          return;
        }
        if (this.device.analogInd === 'Y') {
          if (this.device.extInfo !== undefined && this.device.extInfo !== null) {
            this.device.extInfo = undefined;
          }
        } else if (this.device.deviceType === 'surveillance_camera') {
          if (this.device.iotGatewayId !== undefined && this.device.iotGatewayId !== null) {
            this.device.iotGatewayId = undefined;
            if (this.device.iotDeviceId !== undefined && this.device.iotDeviceId !== null) {
              this.device.iotDeviceId = undefined;
            }
            if (this.device.unit !== undefined && this.device.unit !== null) {
              this.device.unit = undefined;
            }
            if (this.device.upLimit !== undefined && this.device.upLimit !== null) {
              this.device.upLimit = undefined;
            }
            if (this.device.downLimit !== undefined && this.device.downLimit !== null) {
              this.device.downLimit = undefined;
            }
            if (this.device.analogConvertFormula !== undefined && this.device.analogConvertFormula !== null) {
              this.device.analogConvertFormula = undefined;
            }
          }
        } else {
          if (this.device.extInfo !== undefined && this.device.extInfo !== null) {
            this.device.extInfo = undefined;
          }
          if (this.device.unit !== undefined && this.device.unit !== null) {
            this.device.unit = undefined;
          }
          if (this.device.upLimit !== undefined && this.device.upLimit !== null) {
            this.device.upLimit = undefined;
          }
          if (this.device.downLimit !== undefined && this.device.downLimit !== null) {
            this.device.downLimit = undefined;
          }
          if (this.device.analogConvertFormula !== undefined && this.device.analogConvertFormula !== null) {
            this.device.analogConvertFormula = undefined;
          }
        }
      },

      saveDevice: function (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.device.deviceType === 'smart_smoke_detector' || this.device.deviceType === 'smart_gas_detector'){
              this.device.iotGatewayId='SYSTEM-SENSOR-SMART-SENSOR_GATEWAY-01';
            };
            this.iots();
            this.$http.put(server.api.organization.device.updDevice, this.device).then((response) => {
              if(!response.body.meta.success){
                this.$Message.error(response.body.meta.message);
                return;
              }
              this.getDevices(this.pageNum);
              this.addDeviceModal = false;
              this.$Message.success('修改成功！');
              this.$refs['deviceFrom'].resetFields();
            }, (error) => {
              this.$Message.error('修改失败！');
            })
          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      changeDeviceType: function (typeCode) {
        if(this.typeCode !== undefined && this.typeCode !== null && this.typeCode.length !== 0) {
          this.changeTypeCode();
        }
        this.typeCode=typeCode;
        for(var i = 0; i<this.deviceTypeList.length; i++){
          let deviceType = this.deviceTypeList[i];
          if(deviceType.typeCode == typeCode){
            this.device.analogInd=deviceType.analogInd;
            this.device.deviceType=deviceType.typeCode;
            if(typeCode==='surveillance_camera'){
              //新增加设备情况下
              if(this.device.deviceId===undefined){
                this.device.extInfo.cameraType = 'YDPNP';
                //更换设备类型
              } else if((this.device.extInfo===undefined || this.device.extInfo===null)||((this.device.extInfo!==undefined && this.device.extInfo!==null) && (this.device.extInfo.cameraType===undefined && this.device.extInfo.cameraType===null))){
                this.device.extInfo={};
                this.device.extInfo.cameraType = 'YDPNP';
              }
            }
            break;
          }
        }
      },

      confirm (camera,flg) {
        this.$Modal.confirm({
          title: '确定要解绑吗',
          onOk: () => {
            if(flg===1){
              this.Unbundling(camera);
            } else if(flg===2) {
              this.delCamera(camera);
            }
          },
          onCancel: () => {}
        });
      },
      custom (camera) {
        this.$Modal.confirm({
          title: '确定要绑定吗',
          onOk: () => {
            this.addCamera(camera);
          },
          onCancel: () => {}
        });
      },

      changeCameraType:function (cameraype) {
        this.probeId = cameraype;
        this.changeProbeId();
      },

      relocate: function(){
        this.showMapLocator = true;
      },
      onCloseMapLocatorModal: function(){
        this.showMapLocator = false;
      },
      onLocationUpdated: function(address) {
        this.build.mapLng = address.location[0];
        this.build.mapLat = address.location[1];
      },
      openDelFloor:function (floor) {
        this.floor=floor;
        this.modal=true;
      },
    },
    computed:{
      editable: function() {
        return this.hasPermission(this.$YDAuthCode.NormalEntity.BuildingMgmt);
      },
      mapLocatorDefaultCenter : function(){
        if(this.build == null || this.build.mapLng == null || this.build.mapLat == null){
          return  [121.5300857178, 38.8626430801];
        }else{
          return [this.build.mapLng ,this.build.mapLat];
        }
      },
      buildingCoordination: function(){
        if(this.build == null || this.build.mapLng == null || this.build.mapLat == null){
          return [];
        }else{
          return [this.build.mapLng ,this.build.mapLat];
        }
      },
    },

    watch: {
      addDeviceModal: function(val, oldVal) {
        if(!val) {
          let deviceContainer = $("#device-container");
          deviceContainer.empty();
          deviceContainer.removeClass("cursor-device");
          this.device.floorId=undefined;
        }
      },
      locationModal: function(val, oldVal) {
        if(val) {
          Vue.nextTick(() => {
            $("#floor_location_device_list").getNiceScroll().show();
            setTimeout(() => {
              $("#floor_location_device_list").getNiceScroll().resize();
            }, 1000);
          });
        } else {
          $("#floor_location_device_list").getNiceScroll().hide();
        }
      },

      baseCamerasByDevice:function () {
        if(this.baseCamerasByDevice.length>5){
          $('#baseCamerasByDevice').addClass('baseCameras');
          Vue.nextTick(() => {
            $(".baseCameras").niceScroll({
              cursorcolor: "rgb(99, 114, 131)",
              cursorwidth: "4px",
              cursoropacitymax: 0.4,
              cursorborder: "",
              cursorborderradius: "",
              hidecursordelay: 1000,
            });
          });
        } else {
          $('#baseCamerasByDevice').removeClass('baseCameras');
        }
      }
    },
    mounted() {
      if(this.editable){
        this.columns.push(
          {
            title: '操作',
            align: 'center',
            width:80,
            render: (h, params) => {
              return h('div', [
                h('i-button', {
                  props: {
                    type: 'text',
                    size: 'small'
                  },

                  style: {
                    color: '#f9caca'
                  },
                  on: {
                    click: () => {
                      this.deleteDevices(params.row.deviceId)
                    }
                  }
                }, '移除')
              ]);
            }
          }
        )
      }
      this.organizationId = this.$route.query.organizationId == undefined ?  localStorage.current_org_id : this.$route.query.organizationId;
      this.tableName = this.$route.query.tableName;
      this.fetchData();
      Vue.nextTick(() => {
        $(".select-type .ivu-select-dropdown").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "4px",
          cursoropacitymax: 0.4,
          cursorborder: "",
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
      });
    }
  }
</script>



